import React, { createRef, useRef } from 'react'
import Glide from '@glidejs/glide'
import imageOne from '../images/1.jpg' 
import imageTwo from '../images/2.jpg' 
import imageThree from '../images/3.jpg' 
import imageFour from '../images/4.jpg' 

export class View extends React.Component{
    constructor (props) {
        super(props)
        this.slider = React.createRef()
    }
    componentDidMount () {
        let glide = new Glide(`.${this.slider.current.classList[0]}`, {
            type: 'carousel',
            perView: 4,
            focusAt: 'center',
            breakpoints: {
            800: {
                perView: 2
            },
            480: {
                perView: 1
            }
            }
        })
        
        glide.mount()
        }
    
    render () {
        return (
            <>
                <div id="about" className="cover__container">
                    <h1 className="cover__h1">Барбершоп <strong className="cover__h1_strong">BARBERGRIB</strong></h1>
                    <p className="cover__description">
                    задумывался как место, где из цены стрижки исключены ненужные расходы(такие как зарплата администратора, большая арендная плата и т.д.) Благодаря этому цена стрижки отличается от средней по городу в более приятную сторону
                    </p>
                    <a href="https://beauty.dikidi.net/ru/record/299867?p=0.pi" className="cover__btn">Записаться</a>
                </div>  
                <div className="gallery__slider" id="gallery">
                    <div ref={this.slider} className="glide">
                        <div className="glide__track" data-glide-el="track">
                            <ul className="glide__slides">
                                <img src={imageOne} className="glide__slide gallery__item" alt="" srcset="" />
                                <img src={imageTwo} className="glide__slide gallery__item" alt="" srcset="" />
                                <img src={imageThree} className="glide__slide gallery__item" alt="" srcset="" />
                                <img src={imageFour} className="glide__slide gallery__item" alt="" srcset="" />
                            </ul>
                        </div>
                    </div>
                </div>
            </>
            
        )
    }
    
}