import React, { createRef, useEffect, useRef } from 'react'

export class BarberPool extends React.Component {
    constructor(props){
        super(props);
        this.pool = React.createRef();
    }
    poolRails = (event) => {
        this.pool.current.style.left = `calc(${event.target.scrollingElement.scrollTop}px - 80%)`
    }
    componentDidMount () {
        window.addEventListener('scroll', event => {this.poolRails(event)})
    }
    componentWillUnmount () {
        window.removeEventListener('scroll')
    }
    
    
    render () {
        return (
            <div className="pool__container">
                <div ref={this.pool} className="pool__img" />
            </div>
        )
    }
}